export const homePageAnim = ()=> {
    const tl = gsap.timeline();
    gsap.to('.hero__image',{
        scrollTrigger: {
          trigger: '.hero',
          start: 'top',
          scrub: true,
        },
        yPercent: -30,
    })
    tl.fromTo('.header__logo',{opacity: 0},{opacity: 1,duration: 1},.2)
    .fromTo('.header__menu li',{opacity: 0},{opacity: 1,stagger: 0.2, duration: 1},.2)
    .fromTo('.hero__title',{ yPercent: 100, opacity: 0 },{ yPercent:0, opacity: 1,duration: 1 },.5)
    .fromTo('.hero__description > p',{ yPercent: 100, opacity: 0 },{ yPercent:0, opacity: 1,duration: 1},1)
    .fromTo('.hero__image',{opacity: 0},{opacity: 1,duration: 1},1.5)
    .fromTo('.hero .marquee',{opacity: 0},{opacity: 1,duration: 1},2)

    // request section animation

    gsap.from('.requests__title h2',{
        scrollTrigger: {
          trigger: '.requests__title h2',
            start: '+200px bottom',
        },
        yPercent: 50,
        opacity: 0,
        duration: 0.5,
    })

    gsap.from('.requests__item',{
        scrollTrigger: {
          trigger: '.requests__item',
            start: '+200px bottom',
        },
        yPercent: 50,
        opacity: 0,
        duration: 0.5,
        stagger: 0.1
    })

    gsap.from('.requests__btn',{
        scrollTrigger: {
          trigger: '.requests__btn',
            start: '+200px bottom',
        },
        yPercent: 50,
        opacity: 0,
        duration: 0.5,
        stagger: 0.1
    })

    //approaches section animation

    gsap.from('.approaches__title h2',{
        scrollTrigger: {
            trigger: '.approaches__title h2',
            start: '+200px bottom',
        },
        yPercent: 50,
        opacity: 0,
        duration: 0.5,
    })

    gsap.from('.approaches__description p',{
        scrollTrigger: {
            trigger: '.approaches__description p',
            start: '+200px bottom',
        },
        yPercent: 50,
        opacity: 0,
        stagger: .2,
        duration: 0.5,
    })

    // approaches section animation

    gsap.from('.approaches__item:nth-child(1)',{
        scrollTrigger: {
            trigger: '.approaches__item',
            start: '+200px bottom',
        },
        opacity: 0,scale: 0.5,transformOrigin: 'center center'
    })
    gsap.from('.approaches__item:nth-child(2)',{
        scrollTrigger: {
            trigger: '.approaches__item',
            start: '+200px bottom',
        },
        opacity: 0,scale: 0.5,transformOrigin: 'center center',delay: 0.3
    })
    gsap.from('.approaches__item:nth-child(3)',{
        scrollTrigger: {
            trigger: '.approaches__item',
            start: '+200px bottom',
        },
        opacity: 0,scale: 0.5,transformOrigin: 'center center',delay: 0.9
    })
    gsap.from('.approaches__item:nth-child(4)',{
        scrollTrigger: {
            trigger: '.approaches__item',
            start: '+200px bottom',
        },
        opacity: 0,scale: 0.5,transformOrigin: 'center center',delay: 0.6
    })


    gsap.from('.approaches__btn',{
        scrollTrigger: {
            trigger: '.approaches__btn',
            start: '+200px bottom',
        },
        yPercent: 100,
        opacity: 0,
        duration: 0.5,
    })

    // courses section animation

    gsap.from('.courses__title h2',{
        scrollTrigger: {
            trigger: '.courses__title h2',
            start: '+200px bottom',
        },
        yPercent: 50,
        opacity: 0,
        duration: 0.5,
    })

    gsap.from('.courses__item',{
        scrollTrigger: {
            trigger: '.courses__item',
            start: '+200px bottom',
        },
        yPercent: 10,
        scale: 0.7,
        opacity: 0,
        stagger: .3,
        transformOrigin: 'center center',
        duration: 0.5,
    })

    // consultaion section animation

    gsap.from('.consultation',{
        scrollTrigger: {
            trigger: '.consultation',
            start: '+200px bottom',
        },
        yPercent: 30,
        opacity: 0,
        duration: 0.5,
    })
    gsap.from('.consultation__decoration',{
        scrollTrigger: {
            trigger: '.consultation__decoration',
            start: '+200px bottom',
            end: '100% top',
            scrub: true,
        },
        rotate: 100,
        ease: 'none',
    })

    // advantages section animation
    const advantageImgAnimArea = window.innerHeight + document.querySelector('.advantages__image')?.getBoundingClientRect().height
    gsap.to('.advantages__image',{
        scrollTrigger: {
            trigger: '.advantages__image',
            start: '+10% bottom',
            end: `+=${advantageImgAnimArea}px`,
            scrub: true,
        },
        yPercent: -30,
    })

    gsap.from('.advantages__title h2',{
        scrollTrigger: {
            trigger: '.advantages__title h2',
            start: '+200px bottom',
        },
        yPercent: 50,
        opacity: 0,
        duration: 0.5,
    })

    gsap.utils.toArray('.advantages__item').forEach(item => {
        gsap.from(item, {
            scrollTrigger: {
                trigger: item,
                start: '+200px bottom',
            },
            xPercent: -100,
            stagger: .2,
        })
    })

    // mentor section animation

    gsap.from('.mentor__title h2',{
        scrollTrigger: {
            trigger: '.mentor__title h2',
            start: '+200px bottom',
        },
        yPercent: 50,
        opacity: 0,
        duration: 0.5,
    })

    gsap.from('.mentor h5',{
        scrollTrigger: {
            trigger: '.mentor h5',
            start: '+200px bottom',
        },
        yPercent: 50,
        opacity: 0,
        duration: 0.5,
    })

    gsap.from('.mentor__image',{
        scrollTrigger: {
            trigger: '.mentor__image',
            start: '+200px bottom',
        },
        scale: 0,
        transformOrigin: 'left top',
        opacity: 0,
        duration: 0.5,
    })

    gsap.from('.mentor__description',{
        scrollTrigger: {
            trigger: '.mentor__description',
            start: '+200px bottom',
        },
        scale: 0,
        transformOrigin: 'right top',
        opacity: 0,
        duration: 0.5,
    })

    gsap.from('.mentor__inst',{
        scrollTrigger: {
            trigger: '.mentor__inst',
            start: '+200px bottom',
        },
        yPercent: 100,
        opacity: 0,
        duration: 0.5,
    })

}