export const reviewsSlider = ()=> {

    // slider
    const swiper = new Swiper('.reviews__swiper', {
        autoHeight: true,
        loop: true,
        spaceBetween: 15,
        navigation: {
            nextEl: '.reviews__swiper-next',
            prevEl: '.reviews__swiper-prev',
        },
        breakpoints: {
            768: {
                slidesPerView: 2,
                spaceBetween: 30,
                autoHeight: false,
            },
            1024: {
                slidesPerView: 2,
                spaceBetween: 30,
                autoHeight: false,
            },
            1280: {
                autoHeight: false,
                slidesPerView: 3,
                spaceBetween: 30,
                navigation: {
                    nextEl: '.reviews__swiper-desk-next',
                    prevEl: '.reviews__swiper-desk-prev',
                }
            }
        }
    });

}